<template>
	<view class="content" style="padding: 23rpx 20rpx;">
		<view class="content_top">
			<view class="left">
				<text>劳务交流群</text>
				<image @click="bigimg(code)" :src="code" mode="" style="position: absolute;top: 32rpx; right: 18rpx;"></image>
			</view>
			<view class="right">
				<text>微信客服</text>
				<image @click="bigimg(codetwo)" :src="codetwo" mode="" style="position: absolute;top: 32rpx; right: 18rpx;"></image>
			</view>
		</view>
		<view class="content_main">
			<image :src="bannerImg" mode="scaleToFill"></image>
		</view>
		<view class="content_footer">
			<view class="footer">
				<view class="imag_logo">
					<image src="../../static/imgs/lauwtongxun.png" style="height: 33rpx; width: 144rpx;" mode="aspectFit"></image>
				</view>
				<view class="" style="margin-top:15rpx;">
					<mSearch style="display: inline-block;" @changes="changes"></mSearch>
					<view class="contry" style="display: inline-block;"  @click="openAddres">
						<view class="city" style="display: inline-block; width: 100%; font-size: 30rpx; color: #333; text-align: center;">
							{{city?city:'选择地区'}}
						</view>
						<!-- <text style="margin-left: 10rpx;">{{city}}</text> -->
					</view>
				</view>
				
				<view class="table">
				  <view class="tr bg-w" style="margin-top: 10rpx;">
				    <view class="th" style="width: 16%;">头像</view>
				    <view class="th" style="width: 20%;">公司</view>
				    <view class="th" style="width: 13%;">姓名</view>
					<view class="th" style="width: 24%;">地址</view>
					 <view class="th" style="width: 27%;">操作</view>
				  </view>
				  <block v-for="(item,index) in searchData" :key="index">
				    <view class="tr bg-g" style="margin-top: 8rpx;" >
				      <view class="td" style="width: 16%;"><image style="width:66rpx; height:66rpx;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;" :src="item.isUser==0?'https://www.xn--4gqr6isbv1bn21d.com'+item.wxQcode:item.wxPicture"
							 mode="aspectFit"></image></view>
				      <view class="td" style="width: 20%; font-size: 26rpx; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;">{{item.companyName}}</view>
				      <view class="td" style="width: 13%; font-size: 26rpx; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;">{{item.name}}</view>
					   <view class="td" style="width: 24%; font-size: 26rpx; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;">{{item.city}}</view>
					  <view class="td" style="display: flex; justify-content: space-around; width: 27%;">
						  <view class="" style="display: flex; flex-direction: column; align-items: center;" @click="guanzhu(item)">
						  	<image src="../../static/imgs/icon_yiguanzhu.png" v-if="item.focusData==1" mode="aspectFit" style="height: 36rpx; width: 32rpx;"></image>
							<image src="../../static/imgs/icon_guanzhulaowu.png" v-else mode="aspectFit" style="height: 36rpx; width: 32rpx;"></image>
							<text style="font-size: 24rpx; min-width: 66rpx;">{{item.focusData==1?'已关注':'关注'}}</text>
						  </view>
						  <view class="" style="display: flex; flex-direction: column; align-items: center;" @click="todetail(item.companyId)">
						  	<image src="../../static/imgs/icon_xiangqinglaowu.png" mode="aspectFit" style="height: 36rpx; width: 32rpx;"></image>
							<text style="font-size: 24rpx;">详情</text>
						  </view>
					  </view>
				    </view>
				  </block>
				</view>
				<simple-address ref="simpleAddress" :pickerValueDefault="cityPickerValueDefault" @onConfirm="onConfirm" themeColor="#007AFF"></simple-address>
			</view>
		</view>
	</view>
</template>

<script>
	import mSearch from '@/components/mehaotian-search-revision/mehaotian-search-revision.vue';
	import simpleAddress from '@/components/simple-address/simple-address.vue';
	export default {
		data() {
			return {
				title: 'Hello',
				flag: true,
				bannerList: [], //轮播图数据
				newList: [], //新闻资讯滚动
				rollContent: "", //滚动信息
				hrimg: '',
				zhanlv: '',
				rippleStyle: '',
				falgs: false,
				loading: false,
				showValue: 'name', // 需要显示的数据，必须与infoList中的name对应
				searchValue: '',
				infoList: [],
				video: '',
				cityPickerValueDefault: [0, 0, 1],
				pickerText: '',
				kefucode: '', //码
				code: '', //laowu
				link: "", //跳转小程序
				bannerimg: "", //跳转的图片
				nweList: [],
				text: "",
				city:'', //城市
				bannerImg:'',//跳转图片，
				codetwo:''
				
				
			}
		},
		methods: {
			bigimg(items) {
				const str=items.toString()
				wx.previewImage({
					 current:str, // 当前显示图片的http链接
					urls:[str] // 需要预览的图片http链接列表
				})
			},
			changes(a) {
				this.text = a
			},
			guanzhu(item) { //关注
			var userid=uni.getStorageSync('userId')
				this.$http.postRequest('/company/edit',{companyId:item.companyId,userId:userid.userId,focusData:item.focusData}).then(res=>{
					if(res.msg==1) {
						uni.showToast({
							title:"关注成功",
							success() {
								 item.focusData=1
							}
						})
					}
					if(res.msg==0) {
						uni.showToast({
							title:"取消关注",
							success() {
								item.focusData=0
							}
						})
					}
				})
			},
			openAddres() {
				this.$refs.simpleAddress.open();
			},
			onConfirm(e) {
				var that=this
				that.city=e.labelArr[1]
				that.$http.postRequest('/company/query', {
					province: e.labelArr[0],
					city:e.labelArr[1]
				}).then(res => {
					if(res.length==0) {
						uni.showToast({
							title:"未搜索到结果",
							icon:'none',
							success() {
								that.infoList=[]
							}
						})
					}
					that.infoList=res
				})
			},
			todetail(item) {
				uni.navigateTo({
					url: `../detail/detail?companyId=${item}`
				})
			},
			getmation() {
				var userid = uni.getStorageSync('userId')
				var companyId = uni.getStorageSync('companyId')
				this.$http.postRequest('/company/query',{userId:userid.userId}).then(res => {
					console.log(res)
					this.infoList = res
				}) //所有的列表
			},
			getimg() {
				this.$http.postRequest('/banner/query').then(res=>{        //跳转网页
					this.link=res.linkAddr
					this.bannerImg='https://www.xn--4gqr6isbv1bn21d.com'+res.bannerImg
				})
				this.$http.postRequest('/communicate/query').then(res=>{   //劳务交流二维码
					 this.code=res.communicationQcode
				})
				this.$http.postRequest('/client/query').then(res=>{   //客服交流二维码
				console.log(res)
					this.codetwo=res.clientQcode
				})
			}
		},
		onLoad() {
			this.getmation()
			this.getimg()
		},
		components: {
			mSearch,
			simpleAddress
		},
		computed: {
			searchData() {
				console.log(this.text.toString())
				let searchInputValue = this.text.toString();
				if (searchInputValue) {
					const result = this.infoList.filter(function(daily) {
						console.log(daily)
						// return Object.keys(daily).some(function(key) {
						// 	return String(daily[key]).toLowerCase().indexOf(searchInputValue) > -1;
						// })
						const arr=['companyName','name','province','city']
						 
						 for(let i =0; i< arr.length; i++){
							 if(daily[arr[i]].indexOf(searchInputValue) > -1){
								 return daily
							 }
						 }
						 
					})
					return result
				}
				//如果不存在，整体返回
				return this.infoList;
			}

		}
	}
</script>

<style scoped lang="scss">
	.table {
	  border: 0px solid darkgray;
	}
	.tr {
	  display: flex;
	  width: 100%;
	  justify-content: center;
	  border-bottom: 1rpx solid rgb(245,245,245);
	  // height: 3rem;
	  align-items: center;
	}
	.td {
	    width:40%;
		font-weight: 500;
	    justify-content: center;
	    text-align: center;
	}
	.th {
	  width: 40%;
	  justify-content: center;
	  color:rgba(190, 142, 88, 1);
	  display: flex;
	  border: 1rpx solid rgb(245,245,245);
	  // height: 72rpx;
	  align-items: center;
	}
	
	.text_bar {
		margin: 16rpx 14rpx;
		margin-bottom: 0;
		border-radius: 20px;
		box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.1);
		min-height: 217rpx;
	
		.text_jie {
			text-indent: 2em;
			font-size: 24rpx;
			color: #343434;
			margin: 17rpx 35rpx;
			margin-bottom: 8rpx;
			font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
		}
	}
	
	
	.content_top {
		display: flex;
		justify-content: space-between;

		view {
			height: 140rpx;
			width: 340rpx;
			border-radius: 8px;
			color: #FFFFFF;
			font-size: 30rpx;
			position: relative;

			text {
				margin-left: 23rpx;
				margin-top: 18rpx;
			}

			image {
				width: 98rpx;
				height: 98rpx;
			}
		}
	}

	.right {
		background-size: 100% 100%;
		background-image: url(data:image/png;base64,);
	}

	.left {
		background-size: 100% 100%;
		background-image: url(data:image/png;base64,);
	}

	.content_main {
		height: 182rpx;
		margin-top: 20rpx;
		position: relative;

		image {
			height: 100%;
			width: 100%;
		}
	}

	.btn {
		width: 153rpx;
		height: 40rpx;
		border: 2px solid rgb(140, 156, 177);
		border-radius: 10px;
		color: rgb(140, 156, 177);
		font-size: 20rpx;
		position: absolute;
		bottom: 7rpx;
		left: 275rpx;
		text-align: center;
		line-height: 40rpx;
	}

	.footer {
		margin: 20rpx 14rpx;
		box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.15);
		padding-bottom: 13rpx;
		border-radius: 20px;
		min-height: 800rpx;
	}

	.footer_bar {
		display: flex;
		font-size: 20rpx;
		margin: 13rpx 14rpx;
		margin-left: 0;
		// padding-left: 32rpx;
		justify-content: space-between;
		align-items: center;
		color: #343434;
		border-bottom: 1px solid rgb(224, 223, 223);
		padding-bottom: 15rpx;
		margin-left: 32rpx;
	}

	.imag_logo {
		padding-left: 32rpx;
		padding-top: 24rpx;
	}

	.contry {
		margin-right: 20rpx;
		width: 188rpx;
		font-size: 20rpx;
		padding-left: 15rpx;
		padding-right: 15rpx;
		height: 60rpx;
		color: #3D3D3D;
		border-radius: 8px;
		float: right;
		line-height: 60rpx;
		box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.1);
	}
</style>
